import { HEIGHT, ParameterRadioGroup, WIDTH } from '@engine/controller';
import { FrListener, shortcut } from '@engine/core';
import { AbstractParameterCellControllerWidget } from '../cell_controller.abstract';
import { fetchCellGroupControlAttr } from '../cell_controller.crud';

@shortcut()
export class CellRadioGroup extends AbstractParameterCellControllerWidget {
    public static xtype = 'report.main.table_cell.controller.radio_group';

    public props: {
        position: {
            row: number;
            column: number;
        };
        reportIndex: number;
        xlisteners: FrListener[];
        width: number;
        height: number;
        fontSize: number;
        baseCls: string;
    } = {
        width: WIDTH,
        height: HEIGHT,
        fontSize: 12,
        reportIndex: 0,
        position: {
            row: 0,
            column: 0,
        },
        xlisteners: [],
        baseCls: '',
    }

    private group: ParameterRadioGroup;

    public render() {
        const { xlisteners, position, reportIndex, baseCls, ...opts } = this.options;

        return <Partial<ParameterRadioGroup['props']>>{
            ...opts,
            type: ParameterRadioGroup.xtype,
            ref: (ref: ParameterRadioGroup) => {
                this.group = ref;
            },
            listeners: xlisteners,
            controlAttrFetcher: fetchCellGroupControlAttr,
            extraOptsGetter: () => {
                return {
                    location: {
                        ...position,
                        reportIndex,
                    },
                };
            },
            beforeRender(this: ParameterRadioGroup, cb: Function) {
                this.updateControlAttr()
                    .then(() => cb());
            },
        };
    }

    // 复写父类不触发afterinit事件
    public mounted() {}

    public getValue() {
        return this.group.getValue();
    }

    public postGetValidation() {
        return this.group.postGetValidation();
    }

    public preGetValidation() {
        return this.group.preGetValidation();
    }

    public async setControllerValue(v: string | number | (string | number)[]) {
        await this.group.setControllerValue(v);
    }

    public reset() {
        this.group.reset();
    }

    public postValidateAndToast() {
        return this.group.postValidateAndToast();
    }

    public setInteractValue(v: {
        data: {
            text: string;
            value: string;
        }[];
        value: string | string[];
    }) {
        this.group.setInteractValue(v);
    }

    public reloadControlAttr() {
        this.group.reloadControlAttr();
    }

    public setStyle() {}
}
